// Next
import Link from "next/link";
import { usePathname } from "next/navigation";
// Utils
import {
  imageUrl,
  salesAndSupport,
  sellerLogin,
  sellerRegister,
} from "@/utilities/url";
// 3rd Party
import { Dialog, DialogPanel, PopoverGroup } from "@headlessui/react";
import { Bars3Icon, XMarkIcon } from "@heroicons/react/24/outline";
import { useSelector } from "react-redux";
// Components
import HeaderLogo from "../HeaderLogo";
import { useTranslation } from "react-i18next";

const AuthHeader = ({
  bussinessName,
  storeSettings,
  mobileMenuOpen,
  setMobileMenuOpen,
}) => {
  const pathname = usePathname();
  const { t } = useTranslation();

  const allsettings = useSelector((state) => state.userReducer.allsettings);

  return (
    <header
      className={`bg-white shadow-md shadow-black/860 z-100 sticky top-0 ${
        pathname == "/" && "z-50"
      }`}
    >
      <nav
        className="flex items-center justify-start p-4 mx-auto lg:px-4 max-w-screen-2xl"
        aria-label="Global"
      >
        <div className="flex items-center lg:flex-1 gap-x-8">
          <HeaderLogo
            handleHomePage={() => {}}
            bussinessName={bussinessName}
            storeSettings={storeSettings}
          />
        </div>

        <div className="flex ml-auto lg:hidden rtl:mr-auto rtl:ml-0">
          <button
            type="button"
            className="inline-flex justify-center items-center -m-2.5 p-2.5 rounded-md text-gray-700"
            onClick={() => setMobileMenuOpen(true)}
          >
            <span className="sr-only">{t("homepage.Menu")}</span>
            <Bars3Icon className="w-6 h-6" aria-hidden="true" />
          </button>
        </div>

        <PopoverGroup className="hidden lg:flex lg:gap-8">
          <a
            href={`tel:${salesAndSupport}`}
            className="relative text-sm font-semibold cursor-pointer text-black-300"
          >
            {t("homepage.sales")}
            <span className="text-green-700"> {salesAndSupport}</span>
          </a>

          <a
            href={`tel:${salesAndSupport}`}
            className="relative text-sm font-semibold cursor-pointer text-black-300"
          >
            {t("homepage.support")}
            <span className="text-green-700"> {salesAndSupport}</span>
          </a>
        </PopoverGroup>

        {pathname == "/seller-dashboard" ? (
          <div className="hidden lg:flex lg:flex-0 lg:justify-end space-x-4 rtl:space-x-[unset] ps-[15px]">
            <a
              href={sellerLogin}
              target="_blank"
              className="p-3 text-sm font-semibold leading-6 text-black border rounded cursor-pointer hover:bg-slate-100 rtl:ml-4 border-dark text-dark"
            >
              {t("homepage.Login")}
            </a>

            <a
              href={sellerRegister}
              target="_blank"
              className="bg-[var(--color)] p-3 rounded font-semibold text-white text-sm leading-6 cursor-pointer btn"
            >
              {t("homepage.Signup")}
            </a>
          </div>
        ) : (
          <div className="hidden lg:flex lg:flex-0 lg:justify-end space-x-4 rtl:space-x-[unset] ps-[15px]">
            <Link
              prefetch={false}
              href="/seller-dashboard"
              className="p-3 text-sm font-semibold leading-6 text-black border rounded hover:bg-slate-100 border-dark text-dark"
            >
              {t("homepage.BeSeller")}
            </Link>
          </div>
        )}
      </nav>

      {/* Mobile header */}
      <Dialog
        as="div"
        className="lg:hidden"
        open={mobileMenuOpen}
        onClose={setMobileMenuOpen}
      >
        <div className="fixed inset-0 z-50" />
        <DialogPanel className="fixed inset-y-0 right-0 w-full px-4 py-4 overflow-y-auto bg-white z-150 sm:ring-1 sm:ring-gray-900/10 sm:max-w-sm">
          <div className="flex items-center justify-between">
            <Link
              prefetch={false}
              href="/"
              className="-m-1.5 p-1.5 cursor-pointer"
            >
              <span className="sr-only">{t("homepage.yourCompany")}</span>
              <img
                src={
                  imageUrl +
                  "?path=" +
                  allsettings?.storeLogoPath +
                  "&name=" +
                  allsettings?.storeLogo +
                  "&width=220&height=220"
                }
                alt={allsettings?.storeName}
                className="w-auto max-sm:w-[30vw] h-10 object-contain"
              />
            </Link>

            <button
              type="button"
              className="-m-2.5 p-2.5 rounded-md text-gray-700"
              onClick={() => setMobileMenuOpen(false)}
            >
              <span className="sr-only">{t("homepage.closeMenu")}</span>
              <XMarkIcon className="w-6 h-6" aria-hidden="true" />
            </button>
          </div>

          <div className="flow-root mt-6">
            <div className="-my-6 divide-y divide-gray-500/10">
              <div className="py-4 space-y-2">
                <a
                  href={`tel:${salesAndSupport}`}
                  className="block px-3 py-2 -mx-2 text-base font-semibold leading-7 text-gray-900 rounded-lg cursor-pointer hover:bg-gray-50 style-2"
                >
                  {t("homepage.sales")}
                  <span className="text-green-700"> -{salesAndSupport}</span>
                </a>

                <a
                  href={`tel:${salesAndSupport}`}
                  className="block hover:bg-gray-50 -mx-2 !mt-0 px-3 py-2 rounded-lg font-semibold text-gray-900 text-base leading-7 cursor-pointer style-2"
                >
                  {t("homepage.support")}
                  <span className="text-green-700"> {salesAndSupport}</span>
                </a>
              </div>

              {pathname == "/seller-dashboard" ? (
                <div className="py-4">
                  <a
                    href={sellerLogin}
                    target="_blank"
                    className="block hover:bg-gray-50 -mx-2 px-3 py-2.5 rounded-lg font-semibold text-gray-900 text-base leading-7 btn"
                  >
                    {t("homepage.Login")}
                  </a>
                </div>
              ) : (
                <div className="py-6">
                  <Link
                    prefetch={false}
                    href="/seller-dashboard"
                    className="block hover:bg-gray-50 -mx-2 px-3 py-2.5 rounded-lg font-semibold text-gray-900 text-base leading-7 btn"
                  >
                    {t("homepage.BeSeller")}
                  </Link>
                </div>
              )}
            </div>
          </div>
        </DialogPanel>
      </Dialog>
    </header>
  );
};

export default AuthHeader;
