"use client";
// Next
import Link from "next/link";
import { useTranslation } from "react-i18next";

const ShopByCategory = ({
  sortedTopCategories,
  handleCategoryNames,
  setAllCategory,
  setSubCategory,
  setSubChildCategory,
  setSubCategoryData,
}) => {
  const { t } = useTranslation();
  return (
    <div className="!mt-0">
      <h3 className="block -mx-2 !mt-0 px-3 py-2 rounded-lg font-semibold text-gray-900 text-base leading-7">
        {t("allcategory.shopByCategory")}
      </h3>
      <div>
        {sortedTopCategories?.slice(0, 5)?.map((data, index) => (
          <span
            key={index}
            className="group flex justify-between items-center gap-[10px] p-[8px] cursor-pointer"
          >
            <Link
              prefetch={false}
              href={`/product/${data?.categorySlug}`}
              className="font-semibold text-[14px] text-gray-900 hover:underline cursor-pointer"
              onClick={() =>
                handleCategoryNames(data?.name, data?.categorySlug)
              }
            >
              {data?.categoryNameTrans ? data?.categoryNameTrans : data?.name}
            </Link>
            <span
              className="hover:bg-[#e6e6e6] p-3 rounded text-gray-400 text-xl"
              onClick={(e) => {
                setAllCategory(false);
                setSubCategory(true);
                setSubChildCategory(false);
                setSubCategoryData(data);
              }}
            >
              <img src="/img/categories-arrow.svg" alt="arrow" />
            </span>
          </span>
        ))}

        <a
          role="button"
          onClick={(e) => setAllCategory(true)}
          className="group flex justify-between items-center gap-[10px] p-[8px] w-fit cursor-pointer"
        >
          <span className="font-semibold text-[14px] text-gray-900 hover:underline cursor-pointer">
            {t("allcategory.seeAll")}
          </span>
          <span className="text-xl text-gray-400 rotate-90">
            <img src="/img/categories-arrow.svg" alt="arrow" />
          </span>
        </a>
      </div>
    </div>
  );
};

export default ShopByCategory;
