//React and Next Import
import Link from "next/link";
//Redux
import { useSelector } from "react-redux";
//Components
import { useTranslation } from "react-i18next";

const SellerWishListCheckoutButtons = ({ token, wishCount }) => {
  const { t } = useTranslation();
  const cardpopCount = useSelector((state) => state.userReducer.cardpopCount);

  const handleCartCount = (countCal) => {
    let totalcount = 0;
    countCal?.map((data) => {
      totalcount += data?.productCount;
    });
    return totalcount;
  };

  return (
    <>
      <Link
        prefetch={false}
        href="/seller-dashboard"
        className="relative h-full text-sm font-semibold leading-6 text-black-300 style-2"
      >
        {t("homepage.Seller")}
      </Link>

      <Link
        prefetch={false}
        href={token ? "/wishlist" : "/buyer-login"}
        className="relative flex items-center justify-center w-12 h-12 px-3 py-2 rounded-full cursor-pointer style-2 tooltip4"
      >
        <img src="/img/heART.svg" alt="heart" />
        {wishCount >= 1 && (
          <span className="top-0 -right-0 absolute flex justify-center items-center bg-orange-300 p-0 rounded-full outline outline-1 outline-white group-hover:outline-[#e8e8e8] w-4 h-4 font-normal text-[10px] text-white">
            {wishCount}
          </span>
        )}
        <span className="shadow-md tooltip-text">
          {t("Breadcrumb.wishList")}
        </span>
      </Link>

      <Link
        prefetch={false}
        href="/checkout"
        className="relative flex items-center justify-center w-12 h-12 px-3 py-2 rounded-full cursor-pointer style-2 tooltip4"
      >
        <img src="/img/Mask Group 508.svg" alt="Mask Group 508" />
        {cardpopCount?.length >= 1 && (
          <span className="top-1 -right-0 absolute flex justify-center items-center bg-orange-300 p-0 rounded-full w-4 h-4 font-normal text-[10px] text-white">
            {handleCartCount(cardpopCount)}
          </span>
        )}
        <span className="shadow-md tooltip-text">
          {t("checkout.CartList")}
        </span>
      </Link>
    </>
  );
};

export default SellerWishListCheckoutButtons;
