'use client';

import { useEffect, useState } from 'react';

export default function RuntimeConfigLoader() {
  const [status, setStatus] = useState('loading');

  useEffect(() => {
    const loadRuntimeConfig = async () => {
      try {
        console.log(' Loading runtime configuration...');
        
        // First check if window.__env is already set
        if (window.__env && window.__env.apiBaseUrl) {
          console.log(' window.__env already exists:', window.__env);
          setStatus('already-loaded');
          return;
        }

        // Try to fetch runtime config and set window.__env
        const response = await fetch('/runtime-config.json');
        if (response.ok) {
          const config = await response.json();
          
          // Set window.__env
          window.__env = {
            apiBaseUrl: config.apiBaseUrl,
            imageUrl: config.imageUrl,
            environment: config.environment,
            storeKey: config.storeKey,
            industrySlug: config.industrySlug
          };
          
          console.log(' Loaded runtime config into window.__env:', window.__env);
          setStatus('loaded');
          
          // Force reload axios configuration
          if (window.location.reload) {
            // Don't actually reload, just trigger config refresh
            console.log(' Runtime config loaded, axios should pick up new config');
          }
        } else {
          console.error(' Failed to fetch runtime-config.json');
          setStatus('failed');
        }
      } catch (error) {
        console.error(' Error loading runtime config:', error);
        setStatus('error');
      }
    };

    loadRuntimeConfig();
  }, []);

  // Show status in development
  if (process.env.NODE_ENV === 'development') {
    return (
      <div>
       
      </div>
    );
  }

  return null;
}