// React and  Next
import { Fragment, useMemo, useState } from "react";
import Link from "next/link";
// Utils
import { Cookiescategory } from "@/utilities/cookies";
import { imageUrl } from "@/utilities/url";
import { sortCategories } from "@/utilities/globalFunctions";
// 3rd Party
import {
  Dialog,
  DialogPanel,
  Disclosure,
  DisclosureButton,
  DisclosurePanel,
} from "@headlessui/react";
import { ChevronDownIcon } from "@heroicons/react/20/solid";
import { XMarkIcon } from "@heroicons/react/24/outline";
// Redux
import {
  breadcrumallcategory,
  breadcrumb,
  categoryname,
  productslug,
  subproduct,
} from "@/redux/slices/userSlice";
import { useDispatch, useSelector } from "react-redux";
// Components
import LanguageList from "../LanguageDropdown/LanguageList";
import MyAccountRoutes from "../MyAccountDropdown/MyAccountRoutes";
import ShopByCategory from "./ShopByCategory";
import MobileCategories from "./MobileCategories";
import { useTranslation } from "react-i18next";

function classNames(...classes) {
  return classes.filter(Boolean).join(" ");
}

const MobileHeader = ({
  token,
  mobileMenuOpen,
  setMobileMenuOpen,
  languageList,
  langShow,
  handleLanguage,
  addonList,
  topCategories,
  handleLogout,
}) => {
  const sortedTopCategories = useMemo(
    () => sortCategories(topCategories),
    [topCategories]
  );

  const { t } = useTranslation();
  const allsettings = useSelector((state) => state.userReducer.allsettings);
  const dispatch = useDispatch();

  const [allCategory, setAllCategory] = useState(false);
  const [subCategory, setSubCategory] = useState(false);
  const [subChildCategory, setSubChildCategory] = useState(false);
  const [subCategoryData, setSubCategoryData] = useState({});
  const [subChildCategoryData, setSubChildCategoryData] = useState({});

  const handleCategoryNames = (name, slug) => {
    dispatch(categoryname(name));
    dispatch(breadcrumb(false));
    dispatch(productslug(slug));
    dispatch(breadcrumallcategory(false));
    dispatch(subproduct(false));
    Cookiescategory(name);
    setMobileMenuOpen(false);
    setAllCategory(false);
    setSubCategory(false);
    setSubChildCategory(false);
  };

  return (
    <Dialog
      as="div"
      className="lg:hidden"
      open={mobileMenuOpen}
      onClose={setMobileMenuOpen}
    >
      <div className="fixed inset-0 z-10" />
      <DialogPanel className="fixed inset-y-0 right-0 w-full overflow-y-auto bg-white z-150 sm:ring-1 sm:ring-gray-900/10 sm:max-w-sm">
        <div className="sticky top-0 z-10 flex items-center justify-between p-2 bg-white">
          <Link
            prefetch={false}
            href="/"
            className="-m-1.5 p-1.5 cursor-pointer"
          >
            <span className="sr-only">{t("homepage.yourCompany")}</span>
            <img
              src={
                imageUrl +
                "?path=" +
                allsettings?.storeLogoPath +
                "&name=" +
                allsettings?.storeLogo +
                "&width=220&height=220"
              }
              className="w-auto max-sm:w-[30vw] h-10 object-contain"
            />
          </Link>

          <button
            type="button"
            className="p-2.5 rounded-md text-gray-700"
            onClick={() => {
              setMobileMenuOpen(false);
              setAllCategory(false);
              setSubCategory(false);
              setSubChildCategory(false);
            }}
          >
            <span className="sr-only">{t("homepage.closeMenu")}</span>
            <XMarkIcon className="w-6 h-6" aria-hidden="true" />
          </button>
        </div>

        <div className="grid grid-cols-[100%_100%_100%_100%] overflow-hidden">
          <div
            className={`flow-root transition-all duration-300 px-4 py-4 h-[calc(100vh-60px)] overflow-auto ${
              allCategory || subCategory || subChildCategory
                ? "-translate-x-[100%]"
                : ""
            }`}
          >
            <div className="-my-6 divide-y divide-gray-500/10">
              <div className="py-4 space-y-2">
                <Disclosure as="div" className="-mx-2">
                  {({ open }) => (
                    <Fragment>
                      <DisclosureButton className="flex justify-between items-center hover:bg-gray-50 py-2 pr-3.5 pl-3 rounded-lg w-full font-semibold text-gray-900 text-base leading-7">
                        {langShow ? langShow.name : t("homepage.Language")}
                        <ChevronDownIcon
                          className={classNames(
                            open ? "rotate-180" : "",
                            "h-5 w-5 flex-none"
                          )}
                          aria-hidden="true"
                        />
                      </DisclosureButton>
                      <DisclosurePanel className="mt-2 space-y-2">
                        <DisclosureButton>
                          <LanguageList
                            languageList={languageList}
                            langShow={langShow}
                            handleLanguage={handleLanguage}
                          />
                        </DisclosureButton>
                      </DisclosurePanel>
                    </Fragment>
                  )}
                </Disclosure>

                <Link
                  prefetch={false}
                  href="/seller-dashboard"
                  className="block hover:bg-gray-50 -mx-2 !mt-0 px-3 py-2 rounded-lg font-semibold text-gray-900 text-base leading-7"
                  onClick={() => setMobileMenuOpen(false)}
                >
                  {t("homepage.Seller")}
                </Link>

                <Link
                  prefetch={false}
                  href="/wishlist"
                  className="block hover:bg-gray-50 -mx-2 !mt-0 px-3 py-2 rounded-lg font-semibold text-gray-900 text-base leading-7"
                  onClick={() => setMobileMenuOpen(false)}
                >
                  {t("homepage.wishList")}
                </Link>

                <Link
                  prefetch={false}
                  href="/checkout"
                  className="block hover:bg-gray-50 -mx-2 !mt-0 px-3 py-2 rounded-lg font-semibold text-gray-900 text-base leading-7"
                  onClick={() => setMobileMenuOpen(false)}
                >
                  {t("homepage.CartList")}
                </Link>

                {token &&
                  addonList?.["product-quotation"] &&
                  addonList?.["product-quotation"]?.status == 1 && (
                    <Link
                      prefetch={false}
                      href="/quotation"
                      className="block hover:bg-gray-50 -mx-2 !mt-0 px-3 py-2 rounded-lg font-semibold text-gray-900 text-base leading-7"
                      onClick={() => setMobileMenuOpen(false)}
                    >
                      {t("homepage.quotationList")}
                    </Link>
                  )}

                <ShopByCategory
                  sortedTopCategories={sortedTopCategories}
                  handleCategoryNames={handleCategoryNames}
                  setAllCategory={setAllCategory}
                  setSubCategory={setSubCategory}
                  setSubChildCategory={setSubChildCategory}
                  setSubCategoryData={setSubCategoryData}
                />

                {/* My Account dropdown */}
                {token && (
                  <Disclosure as="div" className="-mx-2">
                    {({ open }) => (
                      <Fragment>
                        {/* My Account heading */}
                        <DisclosureButton className="flex justify-between items-center hover:bg-gray-50 py-2 pr-3.5 pl-3 rounded-lg w-full font-semibold text-gray-900 text-base leading-7">
                          {t("homepage.MyAccount")}
                          <ChevronDownIcon
                            className={classNames(
                              open ? "rotate-180" : "",
                              "h-5 w-5 flex-none"
                            )}
                            aria-hidden="true"
                          />
                        </DisclosureButton>

                        <DisclosurePanel className="mt-2 space-y-2">
                          <DisclosureButton>
                            <MyAccountRoutes
                              from={"mobileView"}
                              setMobileMenuOpen={setMobileMenuOpen}
                              addonList={addonList}
                            />
                          </DisclosureButton>
                        </DisclosurePanel>
                      </Fragment>
                    )}
                  </Disclosure>
                )}
              </div>

              <div className="py-6">
                {token ? (
                  <Link
                    prefetch={false}
                    href="/"
                    className="block hover:bg-gray-50 -mx-2 px-3 py-2.5 rounded-lg font-semibold text-gray-900 text-base leading-7"
                    onClick={handleLogout}
                  >
                    {t("homepage.Logout")}
                  </Link>
                ) : (
                  <>
                    <Link
                      prefetch={false}
                      href="/buyer-login"
                      className="block hover:bg-gray-50 -mx-2 px-3 py-2.5 rounded-lg font-semibold text-gray-900 text-base leading-7"
                      onClick={() => setMobileMenuOpen(false)}
                    >
                      {t("homepage.Login")}
                    </Link>
                    <Link
                      prefetch={false}
                      href="/buyer-signup"
                      className="block hover:bg-gray-50 -mx-2 px-3 py-2.5 rounded-lg font-semibold text-gray-900 text-base leading-7"
                      onClick={() => setMobileMenuOpen(false)}
                    >
                      {t("homepage.Signup")}
                    </Link>
                  </>
                )}
              </div>
            </div>
          </div>

          <MobileCategories
            allCategory={allCategory}
            subCategory={subCategory}
            subChildCategory={subChildCategory}
            subCategoryData={subCategoryData}
            subChildCategoryData={subChildCategoryData}
            setAllCategory={setAllCategory}
            setSubCategory={setSubCategory}
            setSubChildCategory={setSubChildCategory}
            setSubCategoryData={setSubCategoryData}
            setSubChildCategoryData={setSubChildCategoryData}
            sortedTopCategories={sortedTopCategories}
            handleCategoryNames={handleCategoryNames}
          />
        </div>
      </DialogPanel>
    </Dialog>
  );
};

export default MobileHeader;
