// React
import { useEffect, useState } from "react";
// API
import { CustomerProfileDataApi } from "@/api/clientsideaction/myProfile";
// Utils
import { imageUrl } from "@/utilities/url";
// Redux
import { useSelector } from "react-redux";
// Components
import MyAccountRoutes from "./MyAccountRoutes";
import { useTranslation } from "react-i18next";

const MyAccountDropdown = ({
  token,
  setMobileMenuOpen,
  addonList,
  handleLogout,
}) => {
  const { t } = useTranslation();
  const [profileEditData, setProfileEditData] = useState(null);
  const profilestatus = useSelector((state) => state.userReducer.editprofile);

  useEffect(() => {
    if (token) {
      CustomerProfileDataApi(setProfileEditData);
    } else {
      setProfileEditData();
    }
  }, [token, profilestatus]);

  return (
    <div className="group">
      <div className="relative z-50">
        <button className="flex items-center space-x-1 text-sm font-semibold leading-6 group focus-visible:outline-none text-black-300">
          {profileEditData?.avatarPath && profileEditData?.avatar ? (
            <img
              src={
                imageUrl +
                "?path=" +
                profileEditData?.avatarPath +
                "&name=" +
                profileEditData?.avatar +
                "&width=50&height=50"
              }
              alt={profileEditData?.firstName}
              width={8}
              height={8}
              className="object-cover w-8 h-8 rounded-full rtl:ml-1"
            />
          ) : (
            <div className="flex items-center justify-center w-8 h-8 border rounded-full border-grey">
              <p className="text-lg font-semibold text-black">
                {profileEditData &&
                  profileEditData?.firstName?.charAt(0)?.toUpperCase()}
              </p>
            </div>
          )}
          <p>{t("homepage.MyAccount")}</p>
        </button>

        <div>
          <div className="hidden group-hover:block top-full right-0 rtl:right-[unset] rtl:left-0 z-10 absolute bg-white shadow-lg mt-0 rounded ring-1 ring-gray-900/5 w-screen max-w-56 overflow-hidden">
            <div className="p-4">
              <div className="mb-4 border-b border-grey">
                <div className="flex items-center mb-4 space-x-2">
                  {profileEditData?.avatarPath && profileEditData?.avatar ? (
                    <img
                      src={
                        imageUrl +
                        "?path=" +
                        profileEditData?.avatarPath +
                        "&name=" +
                        profileEditData?.avatar +
                        "&width=40&height=40"
                      }
                      alt={profileEditData?.firstName}
                      width={6}
                      height={6}
                      className="object-cover w-6 h-6 rounded-full rtl:ml-1"
                    />
                  ) : (
                    <div className="flex items-center justify-center w-6 h-6 border rounded-full border-grey">
                      <p className="text-lg font-semibold text-black">
                        {profileEditData &&
                          profileEditData?.firstName?.charAt(0)?.toUpperCase()}
                      </p>
                    </div>
                  )}

                  <p className="text-sm font-semibold text-black-300">
                    {t("homepage.Hi")},{" "}
                    {profileEditData &&
                      profileEditData?.firstName?.charAt(0)?.toUpperCase() +
                        profileEditData?.firstName?.slice(1)}
                  </p>
                </div>
              </div>

              <MyAccountRoutes
                from={"desktopView"}
                setMobileMenuOpen={setMobileMenuOpen}
                addonList={addonList}
              />

              <div
                className="group relative flex items-center space-x-2 rtl:space-x-[unset] hover:bg-[#e8e8e8] p-2 rounded-lg text-sm leading-6"
                onClick={handleLogout}
              >
                <div className="flex items-center justify-center flex-none w-4 h-4 rounded-lg rtl:ml-2">
                  <img src="/img/drop-profile1.svg" alt="drop-profile1" />
                </div>
                <div className="flex-auto">
                  <a
                    role="button"
                    className="block text-sm font-normal cursor-pointer text-black-300"
                  >
                    {t("homepage.Logout")}
                  </a>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default MyAccountDropdown;
