'use client';

import { useEffect, useState } from 'react';

export default function ConfigLoader() {
  const [configStatus, setConfigStatus] = useState('checking');

  useEffect(() => {
    const checkConfig = () => {
      console.log(' ConfigLoader checking window.__env:', window.__env);
      
      if (window.__env && window.__env.apiBaseUrl) {
        console.log(' window.__env is available:', window.__env.apiBaseUrl);
        setConfigStatus('loaded');
        
        // Test if axios will use the correct config
        import('../api/config').then(({ getConfig }) => {
          const config = getConfig();
          console.log(' Final config that axios will use:', config);
          
          if (config.apiBaseUrl === window.__env.apiBaseUrl) {
            console.log(' Config is correctly using window.__env values');
          } else {
            console.error(' Config is NOT using window.__env values');
            console.error('   Expected:', window.__env.apiBaseUrl);
            console.error('   Got:', config.apiBaseUrl);
          }
        });
      } else {
        console.warn(' window.__env is not available or empty');
        setConfigStatus('failed');
        
        // Check if the script tag exists
        const envScript = document.querySelector('script[src="/env.js"]');
        if (envScript) {
          console.log(' env.js script tag found in head');
        } else {
          console.error(' env.js script tag not found');
        }
      }
    };

    // Check immediately
    checkConfig();
    
    // Also check after delays in case the script is still loading
    const timeouts = [100, 500, 1000].map(delay => 
      setTimeout(checkConfig, delay)
    );

    return () => {
      timeouts.forEach(clearTimeout);
    };
  }, []);

  // Show a small indicator in development
  if (process.env.NODE_ENV === 'development') {
    return (
      <div >
       
      </div>
    );
  }

  return null;
}