"use client";

// React & Next Imports
import { useState, useEffect } from "react";
import { useTheme } from "next-themes";

const ColorPalette = () => {
  const themes = [
    {
      label: "Default",
      value: "default",
      color: "#3498DB",
    },
    {
      label: "Amethyst",
      value: "amethyst",
      color: "#9B59B6",
    },
    {
      label: "Java",
      value: "java",
      color: "#1ABC9C",
    },
    {
      label: "Midnight Blue",
      value: "midnightBlue",
      color: "#2C3E50",
    },
    {
      label: "Carrot Orange",
      value: "carrotOrange",
      color: "#E67E22",
    },
    {
      label: "Alizarin",
      value: "alizarin",
      color: "#E74C3C",
    },
  ];

  const [mounted, setMounted] = useState(false);
  const { theme, setTheme } = useTheme("default");
  const [palette, setPalette] = useState(false);

  useEffect(() => {
    setMounted(true);
    if (theme == "system") {
      setTheme("default");
    }
  }, []);

  if (!mounted) return null;

  return (
    <button
      className={` rtl:flex-row-reverse fixed  bottom-0 right-0 z-[100] top-[50%] translate-y-[-50%] flex items-center transition-all duration-300 ${
        palette ? "translate-x-[0px]" : "translate-x-[60px]"
      }  `}
    >
      <div
        className="place-items-center grid bg-[var(--color)] shadow-[0px_5px_15px_#00000029] rounded-[6px_0_0_6px] w-[48px] h-[48px]"
        onClick={(e) => setPalette(!palette)}
      >
        <img src="/img/setting.svg" alt="setting" />
      </div>
      <div className="items-center gap-[10px] grid bg-white shadow-[0px_0px_10px_#0000002C] p-[10px] border border-[#EEEFF1] rounded-[6px_0px_0px_6px] w-[60px] h-fit max-h-[330px]">
        {themes.map((val, index) => (
          <span
            className="place-items-center grid bg-white hover:shadow-[0px_0px_10px_#00000014] rounded-[5px] w-[40px] h-[40px]"
            onClick={() => setTheme(val.value)}
            key={index}
            value={val.value}
          >
            <div
              className="relative rounded-[6px] w-[40px] h-[40px]"
              style={{ backgroundColor: val.color }}
            >
              {theme === val.value && (
                <img
                  src="/img/check.svg"
                  alt="check"
                  className="absolute inset-0 m-auto"
                />
              )}
            </div>
          </span>
        ))}
      </div>
    </button>
  );
};

export default ColorPalette;
