// React
import { Fragment } from "react";
// Utils
import { imageUrl } from "@/utilities/url";

const LanguageList = ({ languageList, langShow, handleLanguage }) => {
  return languageList?.map((item) => (
    <div
      key={item?.name}
      className={`${
        langShow.name == item.name
          ? "relative flex items-center space-x-6 bg-[#e8e8e8] hover:bg-[#e8e8e8] py-1 rounded-lg text-sm leading-6 group ps-2"
          : "relative flex items-center space-x-6 hover:bg-[#e8e8e8] py-1 rounded-lg text-sm leading-6 group ps-2"
      }`}
    >
      <div className="flex-auto">
        <div
          className="flex items-center justify-start space-x-2 font-semibold text-gray-900 cursor-pointer"
          onClick={() => handleLanguage(item)}
        >
          {item?.image ? (
            <img
              src={
                imageUrl +
                "?path=" +
                item?.imagePath +
                "&name=" +
                item?.image +
                "&width=20&height=20"
              }
              alt={item?.name}
              className="rtl:ml-2 w-5 h-[13px]"
            />
          ) : (
            <Fragment>
              {" "}
              <img src="/img/globe.svg" alt="globe" />
            </Fragment>
          )}
          <p>{item?.name}</p>
          <span className="absolute inset-0" />
        </div>
      </div>
    </div>
  ));
};

export default LanguageList;
