"use client";

// React and Next
import { useEffect, useState } from "react";
import Link from "next/link";

// Utils
import { imageUrl } from "@/utilities/url";

// Redux
import { useSelector } from "react-redux";

// Components
import { GetBussinessName } from "@/utilities/localStorage";
import { useTranslation } from "react-i18next";

const HeaderLogo = ({ handleHomePage, storeSettings }) => {
   const { t } = useTranslation();
  const [bussinessName, setBussinessName] = useState(null);
  const allsettings = useSelector((state) => state.userReducer.allsettings);

  // Initialize client-side only data
  useEffect(() => {
    setBussinessName(GetBussinessName());
  }, []);

  return (
    <Link
      prefetch={false}
      href="/"
      onClick={handleHomePage}
      className="-m-1.5 rtl:first:ml-4 p-1.5"
    >
      <span className="sr-only">
        {bussinessName} {t("homepage.store")}
      </span>

      {!['',null,undefined].includes(storeSettings) ? (
        <img
          className="min-w-[152px] max-sm:min-w-[30vw] h-10"
          src={
            imageUrl +
            "?path=" +
            storeSettings?.storeLogoPath +
            "&name=" +
            storeSettings?.storeLogo +
            "&width=300&height=300"
          }
          alt="Spurtcommerce Marketplace"
          onError={({ currentTarget }) => {
            currentTarget.onerror = null;
            currentTarget.src = "/img/nologo.png";
            currentTarget.alt = "logo";
          }}
        />
      ) : (
        !['',null,undefined].includes(allsettings) && (
          <img
            className="min-w-[152px] max-sm:min-w-[30vw] h-10"
            src={
              imageUrl +
              "?path=" +
              allsettings?.storeLogoPath +
              "&name=" +
              allsettings?.storeLogo +
              "&width=300&height=300"
            }
            alt="Spurtcommerce Marketplace"
            onError={({ currentTarget }) => {
              currentTarget.onerror = null;
              currentTarget.src = "/img/nologo.png";
              currentTarget.alt = "logo";
            }}
          />
        )
      )}
    </Link>
  );
};

export default HeaderLogo;
