// Utils
import { imageUrl } from "@/utilities/url";
// 3rd Party
import { Popover, PopoverButton, PopoverPanel } from "@headlessui/react";
// Components
import LanguageList from "./LanguageList";
import { useTranslation } from "react-i18next";

const LanguageDropdown = ({ langShow, languageList, handleLanguage }) => {
 const { t } = useTranslation();

  return (
    <Popover className="relative flex items-center group">
      <PopoverButton className="flex items-center h-full space-x-1 text-sm font-semibold leading-6 bg-transparent focus-visible:outline-0 text-black-300 style-2">
        {langShow && (
          <img
            src={
              imageUrl +
              "?path=" +
              langShow?.imagePath +
              "&name=" +
              langShow?.image +
              "&width=20&height=15"
            }
            alt={`${langShow?.name}-mobile`}
            className="rtl:ml-1 w-5 h-[15px]"
          />
        )}{" "}
        <p>{langShow ? langShow?.name : t("homepage.Language")} </p>
        <img src="/img/caret-down.svg" alt="caret-down" className="relative" />
      </PopoverButton>

      <PopoverPanel className="absolute left-0 z-30 hidden w-screen overflow-hidden bg-white rounded-md shadow-lg group-hover:block top-full ring-1 ring-gray-900/5 max-w-32">
        <div className="p-2.5">
          <LanguageList
            languageList={languageList}
            langShow={langShow}
            handleLanguage={handleLanguage}
          />
        </div>
      </PopoverPanel>
    </Popover>
  );
};

export default LanguageDropdown;
