"use client";
// React and Next
import { useCallback, useEffect, useMemo, useRef, useState } from "react";
import Link from "next/link";
// API
import {
  Productsearch,
  Productsearchpaginate,
} from "@/api/clientsideaction/productsearch";
// Utils
import { imageUrl } from "@/utilities/url";
// 3rd Party
import { useRouter } from "nextjs-toploader/app";
// Redux
import { breadcrumb } from "@/redux/slices/userSlice";
import { useDispatch } from "react-redux";

//Components
import { debounce } from "@/utilities/globalFunctions";
import { useTranslation } from "react-i18next";

export default function HeaderSearch({ searchParams, keyword, setKeyword }) {
  const { t } = useTranslation();
  let router = useRouter();
  let dispatch = useDispatch();
  const resultsRef = useRef(null);
  const [focusedIndex, setFocusedIndex] = useState(0);
  const [productSearchOffset, setProductSearchOffset] = useState([]);
  const [searchShow, setSearchShow] = useState(false);
  const [productsearch, setProductsearch] = useState([]);
  //Search Pagination
  const [offset, setOffset] = useState(0);
  const [scrollLoader, setScrollLoader] = useState(true);
  const [productLoader, setProductLoader] = useState(false);

  const searchslug = productsearch?.map((data) => {
    data?.categorySlug;
  });

  useEffect(() => {
    document.addEventListener("click", handleClick);
    return () => document.removeEventListener("click", handleClick);
    function handleClick(e) {
      setSearchShow(false);
    }
  }, []);

  useEffect(() => {
    if (keyword == "") {
      setSearchShow(false);
    }
  }, [keyword]);

  useEffect(() => {
    if (offset != 0 && productSearchOffset?.length == 10) {
      Productsearchpaginate(
        keyword,
        setProductsearch,
        offset,
        setProductSearchOffset,
        setScrollLoader,
      );
    }
  }, [offset, keyword, productSearchOffset?.length]);

  const createQueryString = useCallback(
    (name, value) => {
      const params = new URLSearchParams(searchParams?.keyword?.toString());
      params.set(name, value);

      return params.toString();
    },
    [searchParams],
  );

  const handleKeyDown = (event, slug) => {
    if (event.key === "ArrowDown") {
      if (focusedIndex >= -1 && focusedIndex < productsearch?.length - 1) {
        setFocusedIndex(focusedIndex + 1);
        const element = document.getElementById(focusedIndex + 1);
        if (element) {
          element.scrollIntoView({ behavior: "smooth", block: "center" });
        }
        if (focusedIndex === productsearch.length - 1) {
        }
      } else {
        setFocusedIndex(-1);
      }
    } else if (event.key === "Enter") {
      if (focusedIndex >= 0 && focusedIndex < productsearch?.length) {
        setFocusedIndex(-1);
        router.push(`/products/${productsearch[focusedIndex].productSlug}`);
        setKeyword(productsearch?.[focusedIndex]?.name);
        setSearchShow(false);
        dispatch(breadcrumb(true));
      } else {
        router.push("/product?" + createQueryString("keyword", keyword));
        setSearchShow(false);
        setKeyword("");
        setFocusedIndex(-1);
      }
    } else if (event.key === "ArrowUp") {
      if (focusedIndex >= -1 && focusedIndex < productsearch?.length) {
        const element = document.getElementById(focusedIndex - 1);
        if (element) {
          element.scrollIntoView({ behavior: "smooth", block: "center" });
        }
        setFocusedIndex(focusedIndex - 1);
        if (focusedIndex === -1) {
          resultsRef.current.scrollTop = 0;
        }
      } else {
        if (focusedIndex <= -1) {
          setFocusedIndex(productsearch?.length - 1);
        }
      }
    }
  };
  const debouncedProductSearchApi = useMemo(
    () =>
      debounce(
        (e, setProductsearch, setProductSearchOffset) => {
          // Main list API
          setProductLoader(true);
          if (e?.length !== 0) {
            setSearchShow(true);
          }
          Productsearch(
            e,
            setProductsearch,
            setProductSearchOffset,
            setProductLoader,
          );
        },
        500, // debounce delay
      ),
    [], //  important  only created once
  );

  const handlekey = (e) => {
    setOffset(0);
    setFocusedIndex(-1);
    setKeyword(e);
    debouncedProductSearchApi(e, setProductsearch, setProductSearchOffset);
    if (searchShow) {
      resultsRef?.current?.scrollTo(0, 0);
    }
  };

  const handleScroll = () => {
    const { scrollHeight, scrollTop, clientHeight } = resultsRef?.current;

    if (
      scrollHeight &&
      scrollTop &&
      clientHeight &&
      productSearchOffset?.length > 0 &&
      scrollLoader == true
    ) {
      if (clientHeight + scrollTop + 1 >= scrollHeight) {
        setScrollLoader(false);
        setOffset((previousOffset) => previousOffset + 10);
      }
    }
  };

  const handleDetails = (id, name) => {
    setFocusedIndex(-1);
    dispatch(breadcrumb(true));
    setKeyword(name);
    setSearchShow(false);
  };
  const ProductSkeleton = () => {
    return (
      <div className="flex animate-pulse items-center space-x-4 px-4 py-3">
        {/* Image skeleton */}
        <div className="h-8 w-8 rounded bg-gray-200" />

        {/* Text skeleton */}
        <div className="flex flex-1 flex-col space-y-2">
          <div className="h-3 w-3/4 rounded bg-gray-200" />
          <div className="h-3 w-1/2 rounded bg-gray-200" />
        </div>
      </div>
    );
  };

  return (
    <div className="w-full">
      <div className="relative">
        <div className="pointer-events-none absolute inset-y-0 right-4 flex items-center ps-3 ltr:max-sm:right-[6px] rtl:left-4 rtl:right-[unset] rtl:max-sm:left-[6px]">
          <img src="/img/search.svg" alt="search" />
        </div>
        <form
          autoComplete="off"
          onSubmit={(e) => {
            if (e) {
              e.preventDefault();
            }
          }}
        >
          <input
            type="text"
            id="default-search"
            value={keyword}
            onChange={(e) => handlekey(e.target.value)}
            onKeyDown={(e) => handleKeyDown(e, searchslug)}
            className="max-sm:leading-0 search-bg block w-full border-0 text-gray-900 ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:outline-none sm:text-sm sm:leading-6 ltr:p-[6px_38px_6px_16px] max-sm:ltr:p-[6px_38px_6px_6px] rtl:p-[6px_16px_6px_38px] max-sm:rtl:p-[6px_6px_6px_38px]"
            placeholder={t("homepage.search")}
          />
        </form>
        {searchShow && (
          <div
            className={`absolute left-0 top-full z-50 mt-2 w-full overflow-y-auto rounded bg-white py-3 drop-shadow-md ${
              productsearch?.length == 0 ? "h-25" : "h-72"
            }`}
            ref={resultsRef}
            onScroll={handleScroll}
          >
            {productLoader ? (
              <>
                {[...Array(5)].map((_, index) => (
                  <ProductSkeleton key={index} />
                ))}
              </>
            ) : (
              <>
                {productsearch?.map((data, index) => (
                  <div key={index}>
                    <Link
                      prefetch={false}
                      href={`/products/${data?.productSlug}`}
                      id={index}
                      className={`flex cursor-pointer items-center space-x-4 px-4 py-3 hover:bg-blue-50 ${
                        focusedIndex === index ? "bg-blue-50" : ""
                      }`}
                      onClick={(e) => {
                        handleDetails(data?.id, data?.name);
                      }}
                    >
                      <img
                        src={
                          imageUrl +
                          "?path=" +
                          data?.productImage?.containerName +
                          "&name=" +
                          data?.productImage?.image +
                          "&width=400&height=200"
                        }
                        alt={data?.name}
                        className="h-8 w-8"
                        onError={({ currentTarget }) => {
                          currentTarget.onerror = null;
                          currentTarget.src = "/img/no-image.png";
                          currentTarget.alt = "no-image";
                        }}
                      />
                      <div className="flex flex-col space-y-1">
                        <h6 className="text-black-300 line-clamp-2 text-sm font-medium">
                          {data?.name}
                        </h6>
                      </div>
                    </Link>
                  </div>
                ))}
                {productsearch?.length == 0 && (
                  <div className="mx-auto mt-0 flex min-h-[5vh] items-center justify-center">
                    <div className="flex flex-col items-center justify-center gap-8 p-4 text-center sm:flex-row">
                      <div>
                        <h5 className="text-black-300 mb-1 text-base font-black">
                          {t("myprofile.NoResults")}{" "}
                          <span className="break-all text-green-500">
                            &quot;{keyword}&quot;
                          </span>
                        </h5>
                      </div>
                    </div>
                  </div>
                )}
              </>
            )}
          </div>
        )}
      </div>
    </div>
  );
}
