"use client";

// React and Next
import { useEffect, useState } from "react";
import Link from "next/link";
import { usePathname } from "next/navigation";

// API
import {
  addonapi,
  contactusapi,
  getLanguagelist,
} from "@/api/clientsideaction/getfilter";
import {
  cartListApi,
  wishiListTotalCountApi,
} from "@/api/serversideaction/cartapi";
import { CustomerLogoutApi } from "@/api/clientsideaction/buyerapi";
import { GetAddonList, GetCartList } from "@/utilities/localStorage";

// Cookies
import { Cookieslanguage } from "@/utilities/cookies";
import { ClearCookies } from "@/utilities/cookies";

// 3rd Party
import { Bars3Icon } from "@heroicons/react/24/outline";
import { PopoverGroup } from "@headlessui/react";
import { useRouter } from "nextjs-toploader/app";
// Redux
import { useDispatch, useSelector } from "react-redux";
import {
  setDeliveryAsBillingAddressRedux,
  setLoggedOutAddressRedux,
} from "@/redux/slices/checkout";
import {
  getWishListCount,
  isLoggedinRedux,
  languagecontent,
  languagetriger,
  offsetCount,
  viewCartCount,
} from "@/redux/slices/userSlice";

// Components
import ConnectPlugin from "../connectPlugins";
import CheckoutFlow from "./CheckoutFlow";
import AuthHeader from "./AuthHeader";
import ResetPasswordHeader from "./ResetPasswordHeader";
import HeaderSearch from "./HeaderSearch";
import HeaderLogo from "./HeaderLogo";
import LanguageDropdown from "./LanguageDropdown";
import MyAccountDropdown from "./MyAccountDropdown";
import SellerWishListCheckoutButtons from "./SellerWishListCheckoutButtons";
import MobileHeader from "./MobileHeader";
import i18n from "../../../i18n";
import { useTranslation } from "react-i18next";

export default function Headers({ token, searchParams, topCategories }) {
  const pathname = usePathname();
  const dispatch = useDispatch();
  const router = useRouter();
  const { t } = useTranslation();
  const [mobileMenuOpen, setMobileMenuOpen] = useState(false);
  const [keyword, setKeyword] = useState("");
  const [languageList, setLanguageList] = useState([]);
  const [langShow, setLagnShow] = useState("");
  const [storeSettings, setStoreSettings] = useState();

  const [addonList, setAddonList] = useState(null);
  const [cardList, setCardList] = useState(null);
  const [isClient, setIsClient] = useState(false);

  const qoutedetails = useSelector((state) => state.userReducer.quote);
  const wishCount = useSelector((state) => state.userReducer.wishCount);
  const isLoggedinState = useSelector((state) => state.userReducer.isLoggedin);
  const languagestatus = useSelector((state) => state.userReducer.langcontent);
  const lan = useSelector((s) => s.userReducer.language);

  // Initialize client-side only data
  useEffect(() => {
    setIsClient(true);
    setAddonList(GetAddonList());
    setCardList(GetCartList());
  }, [pathname, token]);

  useEffect(() => {
    if (typeof window !== "undefined") {
      const direction = localStorage.getItem("direction")
        ? localStorage.getItem("direction")
        : "ltr";
      document.body.setAttribute("dir", direction);
    }
  }, []);

  useEffect(() => {
    dispatch(viewCartCount([]));
    if (cardList?.length > 0) {
      dispatch(viewCartCount(cardList));
    } else {
      dispatch(viewCartCount([]));
    }
  }, [cardList, token]);

  useEffect(() => {
    if (languageList?.length > 0) {
      let langSh = languageList?.find((s) => s.code == lan);
      if (langSh) {
        setLagnShow(langSh);
      }
    }
  }, [lan, languageList]);

  useEffect(() => {
    if (
      !pathname.includes("/wishlist") ||
      !pathname.includes("/product") ||
      !pathname.includes("/order-history") ||
      !pathname.includes("/seller-product") ||
      !pathname.includes("/quotation-list")
    ) {
      dispatch(offsetCount(0));
    }

    if (
      !pathname.includes("/checkout") &&
      !pathname.includes("/choose-address") &&
      !pathname.includes("/payment")
    ) {
      dispatch(setDeliveryAsBillingAddressRedux(false));
      dispatch(setLoggedOutAddressRedux(null));
    }
  }, [pathname, dispatch]);

  useEffect(() => {
    getLanguagelist(setLanguageList);
    contactusapi(dispatch, setStoreSettings);
    addonapi();
  }, [pathname, token, dispatch]);

  useEffect(() => {
    if (token) {
      cartListApi(dispatch);
      wishiListTotalCountApi(() => {}, dispatch);
    }
  }, [token, dispatch]);

  const handleLanguage = (data) => {
    dispatch(languagecontent(languagestatus + 1));
    Cookieslanguage(data?.languageId);
    document.cookie = `lang=${data?.code}; path=/`;
    i18n.changeLanguage(data?.code);
    localStorage.setItem("languageId", data?.languageId);

    if (
      data?.code == "en" ||
      data?.code == "fr" ||
      data?.code == "hi" ||
      data?.code == "de" ||
      data?.code == "es" ||
      data?.code == "zh" ||
      data?.code == "it" ||
      data?.code == "ja"
    ) {
      dispatch(languagetriger(data?.code));
      Cookieslanguage(data?.languageId, 1);
      document.body.setAttribute("dir", "ltr");
      localStorage.setItem("direction", "ltr");
    } else if (data?.code == "ar") {
      Cookieslanguage(data?.languageId, 1);
      dispatch(languagetriger(data?.code));
      document.body.setAttribute("dir", "rtl");
      localStorage.setItem("direction", "rtl");
    } else {
      dispatch(languagetriger("en"));
      Cookieslanguage("57", 1);
      document.body.setAttribute("dir", "ltr");
      localStorage.setItem("direction", "ltr");
    }
  };

  const handleHomePage = () => {
    setKeyword("");
  };

  const handleLogout = () => {
    CustomerLogoutApi(LogoutClear);
  };

  const LogoutClear = () => {
    ClearCookies();
    localStorage.clear();
    dispatch(isLoggedinRedux(false));
    dispatch(languagecontent(0));
    dispatch(viewCartCount([]));
    dispatch(getWishListCount(0));
    setMobileMenuOpen(false);
    router.push("/");
  };

  return pathname == "/buyer-login" ||
    pathname == "/buyer-signup" ||
    pathname == "/forgot-password" ||
    pathname == "/seller-dashboard" ? (
    <AuthHeader
      storeSettings={storeSettings}
      mobileMenuOpen={mobileMenuOpen}
      setMobileMenuOpen={setMobileMenuOpen}
    />
  ) : pathname.includes("/checkout") ||
    pathname.includes("/stock-checkout") ||
    pathname.includes("/choose-address") ||
    pathname.includes("/payment") ||
    pathname.includes("/order-placed") ||
    pathname.includes("/payment-success") ||
    pathname.includes("/payment-failure") ? (
    <CheckoutFlow storeSettings={storeSettings} />
  ) : pathname.includes("/reset-password") ? (
    <ResetPasswordHeader storeSettings={storeSettings} />
  ) : (
    // Default header
    <header
      className={`shadow-black/860 z-100 sticky top-0 bg-white shadow-md first-line:${
        pathname == "/" && "z-50"
      }`}
    >
      <nav
        className="mx-auto flex max-w-screen-2xl items-center justify-start p-4 lg:px-4"
        aria-label="Global"
      >
        <div className="flex items-center space-x-4 sm:space-x-8 lg:flex-1 ltr:pr-3 rtl:pl-3">
          <HeaderLogo
            handleHomePage={handleHomePage}
            storeSettings={storeSettings}
          />

          {addonList &&
            addonList?.["gmap"]?.status == 1 &&
            ConnectPlugin.GoogleMaps && <ConnectPlugin.GoogleMaps />}

          <HeaderSearch
            searchParams={searchParams}
            keyword={keyword}
            setKeyword={setKeyword}
          />
        </div>

        <div className="ml-auto flex lg:hidden rtl:ml-0 rtl:mr-auto">
          <button
            type="button"
            className="-m-2.5 inline-flex items-center justify-center rounded-md p-2.5 text-gray-700"
            onClick={() => setMobileMenuOpen(true)}
          >
            <span className="sr-only">{t("homepage.Menu")}</span>
            <Bars3Icon className="h-6 w-6" aria-hidden="true" />
          </button>
        </div>

        <PopoverGroup className="hidden lg:flex lg:items-center lg:gap-x-0">
          <LanguageDropdown
            langShow={langShow}
            languageList={languageList}
            handleLanguage={handleLanguage}
          />

          <SellerWishListCheckoutButtons token={token} wishCount={wishCount} />
        </PopoverGroup>

        {token ? (
          // Logged in
          <div className="lg:flex-0 hidden items-center space-x-4 lg:flex lg:justify-end">
            {addonList?.["product-quotation"] &&
              addonList?.["product-quotation"]?.status == 1 && (
                <Link
                  prefetch={false}
                  href="/quotation"
                  className="style-2 tooltip4 group relative flex h-12 w-12 items-center justify-center p-0"
                >
                  <img
                    src="/img/header-cart.svg"
                    alt="header-cart"
                    className="w-5"
                  />
                  {qoutedetails?.length >= 1 && (
                    <span className="absolute -right-0 top-0 flex h-4 w-4 items-center justify-center rounded-full bg-orange-300 p-0 text-[10px] font-normal text-white outline outline-1 outline-white group-hover:outline-[#e8e8e8]">
                      {qoutedetails?.length}
                    </span>
                  )}
                  <span className="tooltip-text shadow-md">
                    {t("Breadcrumb.quotationList")}
                  </span>
                </Link>
              )}

            {/* My Account dropdown */}
            <MyAccountDropdown
              token={token}
              setMobileMenuOpen={setMobileMenuOpen}
              addonList={addonList}
              handleLogout={handleLogout}
            />
          </div>
        ) : (
          // Logged out
          <div className="lg:flex-0 hidden space-x-4 ps-[15px] lg:flex lg:justify-end rtl:space-x-[unset]">
            <Link
              prefetch={false}
              href="/buyer-login"
              className="border-dark text-dark btn rounded border p-3 text-sm font-semibold leading-6 text-black hover:bg-slate-100 rtl:ml-4"
            >
              {t("homepage.Login")}
            </Link>

            <Link
              prefetch={false}
              href="/buyer-signup"
              className="rounded bg-[var(--color)] p-3 text-sm font-semibold leading-6 text-white"
            >
              {t("homepage.Signup")}
            </Link>
          </div>
        )}
      </nav>

      {/* Mobile view header */}
      <MobileHeader
        token={token}
        mobileMenuOpen={mobileMenuOpen}
        setMobileMenuOpen={setMobileMenuOpen}
        languageList={languageList}
        langShow={langShow}
        handleLanguage={handleLanguage}
        addonList={addonList}
        topCategories={topCategories}
        handleLogout={handleLogout}
      />
    </header>
  );
}
